"use client"

import { useEffect, useState } from "react"
import Link from "next/link"
import { useRouter, useParams } from "next/navigation"

interface Document {
  _id: string
  fileName: string
  fileUrl: string
  documentType: string
}

interface Business {
  _id: string
  name: string
  description: string
  category: string
  status: "pending" | "approved" | "rejected"
  ownerId: { name: string; email: string }
  latitude: number
  longitude: number
  minInvestmentAmount: number
  maxInvestmentAmount: number
  expectedReturnPercentage?: number
  documents: Document[]
  createdAt: string
}

export default function AdminBusinessDetailPage() {
  const router = useRouter()
  const params = useParams()
  const businessId = params.id as string

  const [business, setBusiness] = useState<Business | null>(null)
  const [loading, setLoading] = useState(true)
  const [updating, setUpdating] = useState(false)

  useEffect(() => {
    fetchBusiness()
  }, [businessId])

  const fetchBusiness = async () => {
    try {
      const response = await fetch(`/api/businesses/${businessId}`)
      if (response.ok) {
        const data = await response.json()
        setBusiness(data.data)
      }
    } catch (error) {
      console.error("Failed to fetch business:", error)
    } finally {
      setLoading(false)
    }
  }

  const handleStatusUpdate = async (status: "approved" | "rejected") => {
    setUpdating(true)
    try {
      const response = await fetch(`/api/admin/businesses/${businessId}/status`, {
        method: "PATCH",
        headers: { "Content-Type": "application/json" },
        body: JSON.stringify({ status }),
      })

      if (response.ok) {
        const data = await response.json()
        setBusiness(data.data)
      }
    } catch (error) {
      console.error("Failed to update status:", error)
    } finally {
      setUpdating(false)
    }
  }

  if (loading) {
    return <div className="min-h-screen flex items-center justify-center">Loading...</div>
  }

  if (!business) {
    return <div className="min-h-screen flex items-center justify-center">Business not found</div>
  }

  return (
    <div className="min-h-screen bg-gray-50">
      {/* Header */}
      <header className="bg-white border-b border-gray-200">
        <div className="max-w-4xl mx-auto px-4 py-4">
          <Link href="/admin/businesses" className="text-blue-600 hover:text-blue-700 text-sm mb-2 block">
            ‚Üê Back
          </Link>
          <h1 className="text-2xl font-bold text-gray-900">{business.name}</h1>
        </div>
      </header>

      {/* Main Content */}
      <main className="max-w-4xl mx-auto px-4 py-8">
        <div className="grid grid-cols-1 lg:grid-cols-3 gap-8">
          {/* Business Details */}
          <div className="lg:col-span-2 space-y-6">
            {/* Description */}
            <div className="bg-white rounded-lg shadow-sm p-6">
              <h2 className="text-lg font-semibold text-gray-900 mb-4">Description</h2>
              <p className="text-gray-600">{business.description}</p>
            </div>

            {/* Details Grid */}
            <div className="bg-white rounded-lg shadow-sm p-6">
              <h2 className="text-lg font-semibold text-gray-900 mb-4">Details</h2>
              <div className="grid grid-cols-2 gap-4">
                <div>
                  <p className="text-sm text-gray-600">Category</p>
                  <p className="text-gray-900 font-medium">{business.category || "N/A"}</p>
                </div>
                <div>
                  <p className="text-sm text-gray-600">Owner</p>
                  <p className="text-gray-900 font-medium">{business.ownerId.name}</p>
                </div>
                <div>
                  <p className="text-sm text-gray-600">Min Investment</p>
                  <p className="text-gray-900 font-medium">${business.minInvestmentAmount.toLocaleString()}</p>
                </div>
                <div>
                  <p className="text-sm text-gray-600">Max Investment</p>
                  <p className="text-gray-900 font-medium">${business.maxInvestmentAmount.toLocaleString()}</p>
                </div>
                {business.expectedReturnPercentage && (
                  <div>
                    <p className="text-sm text-gray-600">Expected Return</p>
                    <p className="text-gray-900 font-medium">{business.expectedReturnPercentage}%</p>
                  </div>
                )}
              </div>
            </div>

            {/* Location */}
            <div className="bg-white rounded-lg shadow-sm p-6">
              <h2 className="text-lg font-semibold text-gray-900 mb-4">Location</h2>
              <div className="text-gray-600">
                Latitude: {business.latitude}, Longitude: {business.longitude}
              </div>
              <p className="text-sm text-gray-500 mt-2">Map integration would display here with Leaflet or Mapbox</p>
            </div>

            {/* Documents */}
            <div className="bg-white rounded-lg shadow-sm p-6">
              <h2 className="text-lg font-semibold text-gray-900 mb-4">Documents ({business.documents.length})</h2>
              {business.documents.length === 0 ? (
                <p className="text-gray-600">No documents uploaded</p>
              ) : (
                <div className="space-y-3">
                  {business.documents.map((doc) => (
                    <div
                      key={doc._id}
                      className="flex items-center justify-between p-3 border border-gray-200 rounded-lg"
                    >
                      <div>
                        <p className="font-medium text-gray-900">{doc.fileName}</p>
                        <p className="text-sm text-gray-600 capitalize">{doc.documentType}</p>
                      </div>
                      <a
                        href={doc.fileUrl}
                        target="_blank"
                        rel="noopener noreferrer"
                        className="text-blue-600 hover:text-blue-700 font-medium text-sm"
                      >
                        Download
                      </a>
                    </div>
                  ))}
                </div>
              )}
            </div>
          </div>

          {/* Sidebar - Status & Actions */}
          <div className="lg:col-span-1">
            {/* Status Card */}
            <div className="bg-white rounded-lg shadow-sm p-6 mb-6">
              <h2 className="text-lg font-semibold text-gray-900 mb-4">Status</h2>
              <div
                className={`inline-flex items-center px-3 py-1 rounded-full text-sm font-medium capitalize mb-4 ${
                  business.status === "approved"
                    ? "bg-green-100 text-green-800"
                    : business.status === "rejected"
                      ? "bg-red-100 text-red-800"
                      : "bg-yellow-100 text-yellow-800"
                }`}
              >
                {business.status}
              </div>

              {business.status === "pending" && (
                <div className="space-y-3">
                  <button
                    onClick={() => handleStatusUpdate("approved")}
                    disabled={updating}
                    className="w-full bg-green-600 hover:bg-green-700 disabled:bg-gray-400 text-white font-medium py-2 px-4 rounded-lg transition"
                  >
                    Approve
                  </button>
                  <button
                    onClick={() => handleStatusUpdate("rejected")}
                    disabled={updating}
                    className="w-full bg-red-600 hover:bg-red-700 disabled:bg-gray-400 text-white font-medium py-2 px-4 rounded-lg transition"
                  >
                    Reject
                  </button>
                </div>
              )}
            </div>

            {/* Assign to Investors */}
            {business.status === "approved" && (
              <div className="bg-white rounded-lg shadow-sm p-6">
                <h2 className="text-lg font-semibold text-gray-900 mb-4">Assign to Investors</h2>
                <Link
                  href={`/admin/visibility?business=${businessId}`}
                  className="inline-block bg-blue-600 hover:bg-blue-700 text-white font-medium py-2 px-4 rounded-lg"
                >
                  Manage Access
                </Link>
              </div>
            )}
          </div>
        </div>
      </main>
    </div>
  )
}
